.about {
  width: 100%;
  align-self: center;
  font-size: 22px;
  background-color: white; 
  
}

#A2 {
  min-height: 1500px;
}

.about-photo {
  display: flex;
  width: 40%;
  height: 500px;
  align-items: center;
  justify-content: center;
}

#me {
  object-fit: cover;
}

#me1 {
  object-fit: cover;
}

#me2 {
  object-fit: cover;
}

.box {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

#box-2 {
  background-color: black;
  color: white;
}

.desc {
  width: 40%;
  height: auto;
  color: #515151;
  align-self: center;
}

#skills {
  color: white;
}

@media only screen and (max-width: 500px) {
  .box {
    flex-direction: column;
    height:auto;
    align-items:center;
    padding-top:10px;
    padding-bottom:20px;
  }

  #A2{
    height:auto;
  }

  .desc {
    width: 80%;
    height:auto;
  }

  .about-photo {
    width: 95%;
    height: auto;
  }

  #me2{
    border:2px solid black;
  }

  #me1{
    border:2px solid white;
  }

  #me{
    border:2px solid black;
  }

  #box-2 {
    flex-direction: column-reverse;
    height:auto;
  }
}
