.project {
  height: 350px;
  width: 295px;
  display: flex;
  flex-direction: column;
  font-size: 16px;
}

.project:hover {
  animation: shake 0.5s;
  animation-iteration-count: 1;
}

.none {
  display: none;
}

.links {
  color: white;
  display: flex;
  flex-direction: column;
  width: 295px;
  align-items: flex-end;
}

.link {
  width: 100px;
  padding-right: 15px;
  color: #515151;
  background-color: white;
  cursor: pointer;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.link:hover {
  background-color: #f5f7f8;
  color: black;
  transition-duration: 1s;
}

#horses {
  opacity: 0.85;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

.description {
  height: 100%;
  color: white;
  display: none;
  font-size:18px;
}

.pro-desc {
  padding-left: 10px;
  padding-right: 10px;
}

.pro-title {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  font-size: 20px;
  justify-content: center;
  height: 30px;
}

img {
  height: 350px;
  width: 100%;
  object-fit: cover;
}

.t-1 {
  background-color: white;
  color: #515151;
}

.l-1 {
  background-color: black;
  color: white;
}

.under1 {
  border-bottom: 2px solid white;
}

.under2 {
  border-bottom: 2px solid black;
}

.first{
  margin-top:2px;
}

.l-2 {
  background-color: white;
  color: black;
}

.div-li2 {
  border-bottom: 1px solid white;
}
.div-li1 {
  border-bottom: 1px solid black;
}

.t-2 {
  background-color: black;
  color: white;
}

.tit-1 {
  background-color: black;
  color: white;
}

.tit-2 {
  background-color: white;
  color: black;
}

.div-ul {
  height: 250px;
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  padding-right: 10px;
}

@media only screen and (max-width: 900px) {
  .project {
    width: 95%;
    height: 350px;
    border: 2px solid black;
    margin-bottom: 10px;
  }

  .links {
    width: 100%;
  }

  .project:hover {
    width: 95%;
  }

  .project:hover {
    animation: shake1 0.5s;
    animation-iteration-count: 1;
  }

  .description {
    height: 350px;
  }
}
