.header {
  background-color: black  ;
  color: white;
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: space-between;  
  padding: 0 20px;
}

.navbar {
  font-size: 30px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu {
  display: flex;
}

.nav {
  padding-right:20px;
}

a:link {
  color: white;
}

a:visited {
  color: white;
}

a:hover {
  color: rgb(204, 202, 202);
  transition: 0.5s;
}

a:link {
  text-decoration: none;
}

a:active {
  color: white;
}

@media only screen and (max-width: 900px) {
  .navbar {
    font-size: 20px;
  }
}
