.projects {
  height: 500px;
  display: flex;
  width:100%;
  justify-content: space-around;
  align-items: center;
  background-color: black;
}

.title-p {
  font-size: 22px;
  height: 50px;
  color: white;
  justify-content: flex-start;
  width: 80%;
}

.projects:first-child {
  flex-direction: column;
}

#pros {
  display: flex;
  width: 100%;
  justify-content: space-around;
  height: 100%;
  padding-bottom: 50px;
  align-items: center;
}

.projects-2 {
  background-color: white;
}

@media only screen and (max-width: 900px) {
  .projects {
    height: auto;
    flex-direction: column;
    background-color: white;
  }

  .title-p {
    color: #515151;
  }

  #pros {
    height: auto;
    padding-bottom: 0;
    flex-direction: column;
  }
}
