#contact {
  color: #515151;
  font-size: 22px;
  padding-left: 30px;
}

#github:link {
  color: #515151;
  text-decoration: none;
}

#github:active {
  color: #515151;
}

#github:visited {
  color: #515151;
}
#github:hover {
  color: rgb(204, 202, 202);
  transition: 0.5s;
}

#title-contact {
  margin-bottom: 5px;
  margin-top: 0;
}

.ainfo {
  height: 50px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  color: #515151;
}

.icons {
  padding-left: 30px;
  color: #515151;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
