html {
  scroll-behavior: smooth;
  font-family: "Candara", Times, serif !important;
  background-color: #F5F7F8;
}

.app {
  display: flex;
  flex-direction: column;
}

body {
  margin: 0;
}

#button_top {
  position: fixed;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
  width:30px;
  height:30px;
  background-color: #515A5A ;
} 

#button_top:hover{
  background-color: rgb(180, 178, 178);
  transition: 0.5s;
}

.start {
  visibility: hidden;
}

.update {
  visibility: visible;
}

footer {
  color: white;
  text-align: center;
  height: 20px;
  display: none;
  align-items: center;
  justify-content: center;
}

#w3c {
  border: 0;
  width: 70px;
  height: 20px;
  position: absolute;
  opacity: 0.3;
  left: 0;
  top: 15vh;
  display: none;
}

@media only screen and (max-width: 1100px) {
  footer {
    width: 95vw;
    align-self: center;
  }
}
